form {
    display: grid;
    grid-template-areas: 'input1 input2'
    'buttonWrapp buttonWrapp';
    justify-items: center;
    align-items: flex-start;
    justify-content: center;
    grid-gap: 1em;
    gap: 1em;
}

.ItemForm_button-wrapper__1P53V{
    width: 100%;
    grid-area: buttonWrapp;
}



.ItemForm_error-handler__4RXQk {
    border: 1px solid rgba(201, 42, 42, 0.685);
    background-color: rgba(224, 145, 131, 0.479);
    border-radius: 10px;
    text-align: center;
    margin-top: 1em;
    padding: .5em .5em;
    font-size: 10px;
}

.ItemForm_input-wrapper__dSWBE {
    display: flex;
    flex-direction: column;
    grid-gap: 1em;
    gap: 1em;
}

.ItemForm_input-wrapper__dSWBE input {
    padding: 1em 2em;
    outline: transparent;
}

.ItemForm_input-wrapper__dSWBE:first-child{
    grid-area: input1;
}


.ItemForm_input-wrapper__dSWBE:nth-child(2){
    grid-area: input2;
}
